import { Component, OnDestroy } from '@angular/core';
import { NbThemeService } from '@nebular/theme';
import { PostsService } from '../../../../app/posts.service';
import { Event } from '../../../../app/event';

@Component({
  selector: 'ngx-temperature',
  styleUrls: ['./temperature.component.scss'],
  templateUrl: './temperature.component.html',
})
export class TemperatureComponent implements OnDestroy {

  temperature = 24;
  temperatureOff = false;
  temperatureMode = 'cool';
  public interval : any;
  ioConnection: any;  
  humidity = 87;
  humidityOff = false;
  humidityMode = 'heat';

  colors: any;
  themeSubscription: any;

  constructor(private theme: NbThemeService, private postsService: PostsService) {
    this.themeSubscription = this.theme.getJsTheme().subscribe(config => {
      this.colors = config.variables;
      this.initIoConnection();
    });
/*     this.refreshData();
    this.interval = setInterval(() => { 
        this.refreshData(); 
    }, 100); */
  }
/* 
  refreshData(){
    this.postsService.storeData().subscribe(value => {
      var value = value;
   });
  } */

  private initIoConnection(): void {
    this.postsService.initSocket();

    this.ioConnection = this.postsService.onTemperature()
      .subscribe((message : any) => {
        console.log("temperature", message.temperature);
        this.temperature = message.temperature;
    });


    this.postsService.onEvent(Event.CONNECT)
      .subscribe(() => {
        console.log('connected');
      });

    this.postsService.onEvent(Event.DISCONNECT)
      .subscribe(() => {
        console.log('disconnected');
      });
  }

  ngOnDestroy() {
    this.themeSubscription.unsubscribe();
  }
}
